import React, { useEffect, useState, useRef } from "react";

import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, ObjectKeys, parseDateMMDDYYYY } from "../../../stores/Utils";

import "./dashboard.css"
import { OpenModal } from "../../../components/DialogHRM";
import { getall_payChecksOnFuture, sortPayChecksOnFutures, delete_payChecksOnFuture } from "../services/server_fetching";
import { SvgIcon_Cmpt } from "../../../components";
import DeleteAlert from "./deleteAlert";
import UpdPayChecksOnFuture from "./UpdPayChecksOnFuture";

const Svg = SvgIcon_Cmpt();

let dayIn_mls = 86400000;

const PayChecksOnFutureItem  = ({ data, elmId, handleRefreshAll }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			// let frm2 = {"firstName":"Belisario","lastName":"Gonzalez Arevalo","genre":"Male","dateOfBirth":"06/02/1976","placeOfBirth":{"city":"Holguin","state":"Holguin"},"currentAddress":{"No":"4307","street":"Estate Dr","city":"Louisville","state":"Kentucky","zipcode":"40216"},"dob":202536000000}
			//updStore(frmNm, frm2);
			set_obs(gen16CodeId())
		}
	});

	
	const updObs =  () => {
		set_obs(gen16CodeId())
	}

	

	const refreshAccs =  () => {
		getall_payChecksOnFuture(updObs)
	}


	const update = async () => {
		updStore("upd_pay_check_future", data);
		updStore("upd_pay_check_future_bck", JSON.parse(JSON.stringify(data)));
		let modaldata = {};
		modaldata["zIndex"] = 450;
		modaldata["height"] = "80vh";
		// data['observeResize']=true;
		modaldata["props"] = { minHeight: "1vh", overlay: true };
		modaldata["content"] = <UpdPayChecksOnFuture confirm={refreshAccs} />;
		OpenModal(modaldata);
	}

	


	const deleteComprobante =  () => {
		delete_payChecksOnFuture(data?.payFutureCheckId, handleRefreshAll);
	}
	

	const OpenDeleteAlert =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(Date.now() > lastTimeCallModal){
			updStore("lastTimeCallModal", Date.now()+320);
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "40vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
			modaldata["content"] = <DeleteAlert confirm={deleteComprobante} item={data} />
			OpenModal(modaldata);
		}
	}

	

	let subAcc = sortPayChecksOnFutures( getStore("sub_payChecksOnFuture_group")?.[data.payChecksOnFutureId],"subPayChecksOnFuture");
	
	let provider = getStore("providers_ids")?.[data?.providerId];
	
	
	return (
		<div className="item_acc">
			<div className="_dsplFlx item_box payChecksOnFuture_header">
					<div className="icon_revision tr_bar_5" onClick={update}>
						<Svg 
							name={ riskInPay(data?.date) === 3?"check_circle": riskInPay(data?.date)===2?"alert":"alert"}
							color={riskInPay(data?.date) === 3 ? "var(--hrm_palette-success-fg)":riskInPay(data?.date)===2?"var(--hrm_palette-color-orange-800)":"var(--hrm_palette-selected-nav-text)"}
						/>
					</div>
					<div className="name tr_bar_20">
						{parseDateMMDDYYYY(data?.date)} 
					</div>
					<div style={{margin:"0 10px"}} />
					<div className="code tr_bar_10" >
						{data?. checkOrSlip}
					</div>
					<div className="name tr_bar_20" >
						{data?. document}
					</div>
					<div className="name tr_bar_30" >
						{provider?.name}
					</div>
					<div className="unit tr_bar_10 right_algn" >
						${data?.amount} 
					</div>
					<div className="icon tr_bar_5" style={{marginLeft: 19}} onClick={OpenDeleteAlert}>
						<Svg name={"outline_delete"} color={"#c62828"}/>
					</div>
			</div>
			
		</div>
	)
}
export default PayChecksOnFutureItem;



const riskInPay = (date) =>{
	let todayDate = (new Date()).getTime();
	let diff =  date - todayDate;
	if(diff<dayIn_mls*7){
		return 1;
	}
	else if(diff<dayIn_mls*14){
		return 2;
	}
	return 3;
}